.fixed {
  position: fixed;
  z-index: 99;
  top: 83px; }

.static {
  position: relative;
  top: 0; }

.bottom_fixed {
  align-self: flex-end;
  position: relative;
  z-index: 99; }

.top_fixed {
  align-self: flex-start; }

.js_about_passport_opacity {
  opacity: 0;
  transition: 1s;
  position: relative; }

.js_about_passport_opacity.is-active {
  opacity: 1;
  animation: showLeft 1s; }

@keyframes showLeft {
  from {
    left: -40px; }
  to {
    left: 0; } }
